<template>
	<div class="btn-add-import-note">
		<a-button type="primary" @click="handleAddImportNote">
			<template #icon>
				<PlusOutlined />
			</template>
			Add ImportNote
		</a-button>
	</div>
	<div class="import-note-table">
		<table class="table">
			<thead>
				<tr>
					<th rowspan="2">#</th>
					<th rowspan="2">Provider</th>
					<th colspan="4">StockIn</th>
					<th rowspan="2">Ship</th>
					<th rowspan="2">Total</th>
					<th rowspan="2">Created At</th>
					<th rowspan="2">Action</th>
				</tr>
				<tr>
					<th>Name</th>
					<th>Expiry</th>
					<th>Quantity</th>
					<th>Cost</th>
				</tr>
			</thead>
			<tbody style="text-align:right">
				<tr v-if="Object.keys(importNoteList).length === 0">
					<td colspan="10" style="text-align:center">No data available in table</td>
				</tr>
				<template v-for="(note, noteID, noteIndex) in importNoteList" :key="noteIndex">
					<tr v-for="(stock, goodsID, stockIndex) in note.stockIn" :key="stockIndex">
						<td
							v-if="stockIndex === 0"
							:rowspan="Object.keys(note.stockIn).length"
						>
							{{ noteIndex + 1 }}
						</td>
						<td v-if="stockIndex === 0" :rowspan="Object.keys(note.stockIn).length" style="text-align:left">
							{{ providerList[note.providerID]?.providerName || '-' }}
						</td>
						<td style="text-align:left">{{ goodsList[goodsID]?.goodsName || '-' }}</td>
						<td>
							<p v-for="({}, expiry, index) in stock" :key="index">
								{{ formatDateTime(Number(expiry)) }}
							</p>
						</td>
						<td>
							<p v-for="({ quantity }, expiry, index) in stock" :key="index">
								{{ quantity }}
							</p>
						</td>
						<td>
							<p v-for="({ costPrice }, expiry, index) in stock" :key="index">
								{{ costPrice }}
							</p>
						</td>
						<td v-if="stockIndex === 0" :rowspan="Object.keys(note.stockIn).length" style="">
							{{ note.shippingCost }}
						</td>
						<td v-if="stockIndex === 0" :rowspan="Object.keys(note.stockIn).length">
							{{ note.totalMoney }}
						</td>
						<td v-if="stockIndex === 0" :rowspan="Object.keys(note.stockIn).length">
							{{ formatDateTime(note.createdAt) }}
						</td>
						<td v-if="stockIndex === 0" :rowspan="Object.keys(note.stockIn).length">
							Action
						</td>
					</tr>
				</template>
			</tbody>
		</table>
	</div>
</template>

<script>
import { PlusOutlined } from '@ant-design/icons-vue'
import { importNoteList } from '@/firebase/useImportNote'
import { providerList } from '@/firebase/useProvider'
import { goodsList } from '@/firebase/useWarehouse'
import { MyFormatDateTime } from '@/utils/convert'

export default {
	components: { PlusOutlined },
	setup() {
		return { importNoteList, providerList, goodsList }
	},
	methods: {
		handleAddImportNote() {
			this.$router.push({ name: 'ImportNote-Add', params: {} })
		},
		formatDateTime(time) {
			return MyFormatDateTime(time, 'DD/MM/YY')
		},
	},
}
</script>

<style lang="scss" scoped>
.import-note-table {
	overflow-x: scroll;
}
.btn-add-import-note {
	margin-bottom: 1rem;
	display: flex;
	justify-content: flex-end;
}
.table {
	min-width: 100%;
}
</style>
